.homeSection__name{
    text-align: center;
    font-size: 2rem;
    text-shadow: 2px 2px var(--tertiary-color);
}

.homeSection__description{
    margin-top: 20px;
    text-align: center;
    text-shadow: 20px;
    text-shadow: 2px 2px var(--tertiary-color);
}

.bouncy-arrow a i{
	-webkit-animation: bounce infinite ease-out .1s;
	animation: bounce .5s ease-out 0s infinite alternate;
	font-size: 2rem;
    color: var(--primary-color);
    position: absolute;
    bottom: 10vh;
}

/* ------------------------------------------*/
/* --------------- keyframes --------------- */
/* ------------------------------------------*/
@-webkit-keyframes bounce{
    to {
      margin-bottom: 10px;
    }
  }
@keyframes bounce{
    to {
      margin-bottom: 10px;
    }
  }

.menu{
    padding: 1em;
    z-index: 2;
    cursor: pointer;
    position: fixed;
    top: 0;
    left: 0;
}

.menu__icon{
    font-size: 1.8em;
    color: var(--secondary-color);
}
.menuList {
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    position: absolute;
    width: 100vw;
    height: 100vh;
    font-size: 2em;
    text-align: center;
    background-color: rgba(0, 0, 0, .9);
    z-index: 1;
    visibility: hidden;
    position: fixed;
  }

  .menuList ul {
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    list-style: none;
  }

  .menuList__item a{
    color: #FFF;
    text-decoration: none;
    font-family: 'Roboto Mono', monospace;
    font-weight: bolder;
  }

  .show {
    visibility: visible;
  }
.content__important{
    font-weight: bold;
    color: var(--quartenary-color);
}

.aboutSection__photo{
    width: 300px;
    border-radius: 300px;
    margin: 20px 0 ;
}

/* Media-Queries */
@media screen and (min-width: 768px) {
    .aboutSection__photo{
        margin-right: 50px;

    }
}
.skillSection__contentWrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.skillsSection__content{
    margin: 20px 0;
}

.skillsSection__subtitle{
    color: var(--quartenary-color);
    text-align: center;
    margin-bottom: 20px;
}

/* Media-Queries */
@media screen and (min-width: 768px) {
    .skillsSection__content{
        flex-direction: column;
        justify-content: space-between;
        width: 40%;
        margin: 10px;
        box-sizing: border-box;
    }
    .skillSection__contentWrapper{
        flex-direction: row;
        align-items: baseline;
        flex-wrap: wrap;
        max-width: 1500px;
    }
}
.skillTag{
    background-color: var(--tertiary-color);
    color: var(--quartenary-color);
    padding: 5px;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 3px;
    border: 1px solid var(--secondary-color);
    display: inline-block;
    font-size: 1.2rem;
}
.experiencesSection{
    height: 100%;
}

/* Media-Queries */
@media screen and (min-width: 768px) {
    .experiencesSection__content{
        flex-direction: column;
        width: 100%;
    }
}
.jobComponent{
    /* background-color: var(--tertiary-color); */
    margin: 10px 0;
    padding: 10px;
    background-color: #e6e3f4;
    /* width: 100%; */
}

.jobComponent__company{
    color: var(--quartenary-color);
    margin: 5px 0; 
}
.jobComponent__duration{
    margin: 5px 0; 
}
.jobComponent__skills{
    margin: 15px 0;
}
.jobComponent__title{
    color: var(--primary-color)
}
.jobComponent__description{
    white-space: pre-line;
}

.jobComponent__head{
    margin-bottom: 30px;
}

/* Media-Queries */
@media screen and (min-width: 768px) {
    .jobComponent{
        flex-direction: row;
        position: relative;
    }
    .jobComponent__company{
        max-width: 350px;
    }
    .jobComponent__skills{
        display: flex;
        position: absolute;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        max-width: 400px;
        top: 0px;
        right: 0;
        align-items: center;
        flex-wrap: wrap;
        justify-content: flex-end;

    }
}
/* Media-Queries */
@media screen and (min-width: 768px) {
    .activitiesSection__content{
        flex-direction: column;
        width: 100%;
    }
}
.activityComponent{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: left;
    width: 100%;
    margin: 20px 0;
    padding-bottom: 30px;
    border-bottom: 1px solid var(--quartenary-color);
}

.activityComponent__head{
    
    margin-bottom: 10px;
}

.activityComponent__title{
    color: var(--quartenary-color);
    
}

.activityComponent__date{
    color: var(--primary-color);
    font-size: 1rem;
}

.activityComponent__description{
    color: var(--secondary-color)
}

.activityComponent__description strong{
    font-size: 1rem;
}
.contactSection{
    text-align: center;
    min-height: 80vh;
}

.contactSection__social{
    display: flex;
    align-items: center;
    justify-content: space-around;
    list-style: none;
    flex-wrap: wrap;
    margin-top: 30px;
}

/* Media-Queries */
@media screen and (min-width: 768px) {
    .contactSection__content{
        flex-direction: column;
        width: 100%;
        height: 400px;
        justify-content: space-around;
        padding-top: 100px 
    }
}
.fab{
    color: #FFF;
    font-size: 2rem;
    background: var(--primary-color);
    padding: 10px;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    border-radius: 50px;
    margin: 5px;
}
.footerSection{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--primary-color);
    text-align: center;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
}

.footerSection__title{
    color: #FFF;
    font-size: 1.2rem;
}
