.jobComponent{
    /* background-color: var(--tertiary-color); */
    margin: 10px 0;
    padding: 10px;
    background-color: #e6e3f4;
    /* width: 100%; */
}

.jobComponent__company{
    color: var(--quartenary-color);
    margin: 5px 0; 
}
.jobComponent__duration{
    margin: 5px 0; 
}
.jobComponent__skills{
    margin: 15px 0;
}
.jobComponent__title{
    color: var(--primary-color)
}
.jobComponent__description{
    white-space: pre-line;
}

.jobComponent__head{
    margin-bottom: 30px;
}

/* Media-Queries */
@media screen and (min-width: 768px) {
    .jobComponent{
        flex-direction: row;
        position: relative;
    }
    .jobComponent__company{
        max-width: 350px;
    }
    .jobComponent__skills{
        display: flex;
        position: absolute;
        width: fit-content;
        max-width: 400px;
        top: 0px;
        right: 0;
        align-items: center;
        flex-wrap: wrap;
        justify-content: flex-end;

    }
}