.homeSection__name{
    text-align: center;
    font-size: 2rem;
    text-shadow: 2px 2px var(--tertiary-color);
}

.homeSection__description{
    margin-top: 20px;
    text-align: center;
    text-shadow: 20px;
    text-shadow: 2px 2px var(--tertiary-color);
}

.bouncy-arrow a i{
	-webkit-animation: bounce infinite ease-out .1s;
	-o-animation: bounce infinite ease-out .1s;
	animation: bounce .5s ease-out 0s infinite alternate;
	font-size: 2rem;
    color: var(--primary-color);
    position: absolute;
    bottom: 10vh;
}

/* ------------------------------------------*/
/* --------------- keyframes --------------- */
/* ------------------------------------------*/
@keyframes bounce{
    to {
      margin-bottom: 10px;
    }
  }