.skillSection__contentWrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.skillsSection__content{
    margin: 20px 0;
}

.skillsSection__subtitle{
    color: var(--quartenary-color);
    text-align: center;
    margin-bottom: 20px;
}

/* Media-Queries */
@media screen and (min-width: 768px) {
    .skillsSection__content{
        flex-direction: column;
        justify-content: space-between;
        width: 40%;
        margin: 10px;
        box-sizing: border-box;
    }
    .skillSection__contentWrapper{
        flex-direction: row;
        align-items: baseline;
        flex-wrap: wrap;
        max-width: 1500px;
    }
}