.activityComponent{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: left;
    width: 100%;
    margin: 20px 0;
    padding-bottom: 30px;
    border-bottom: 1px solid var(--quartenary-color);
}

.activityComponent__head{
    
    margin-bottom: 10px;
}

.activityComponent__title{
    color: var(--quartenary-color);
    
}

.activityComponent__date{
    color: var(--primary-color);
    font-size: 1rem;
}

.activityComponent__description{
    color: var(--secondary-color)
}

.activityComponent__description strong{
    font-size: 1rem;
}