.contactSection{
    text-align: center;
    min-height: 80vh;
}

.contactSection__social{
    display: flex;
    align-items: center;
    justify-content: space-around;
    list-style: none;
    flex-wrap: wrap;
    margin-top: 30px;
}

/* Media-Queries */
@media screen and (min-width: 768px) {
    .contactSection__content{
        flex-direction: column;
        width: 100%;
        height: 400px;
        justify-content: space-around;
        padding-top: 100px 
    }
}